.push_button {
  position: relative;
  width: 220px;
  height: 40px;
  text-align: center;
  color: #fff;
  text-decoration: none;
  line-height: 43px;
  font-family: 'Oswald', Helvetica;
  display: block;
  margin: 30px;
}
.push_button:before {
  background: #f0f0f0;
  background-image: -webkit-gradient(
    linear,
    0% 0%,
    0% 100%,
    from(#d0d0d0),
    to(#f0f0f0)
  );

  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;

  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5) inset, 0 1px 0 #fff;
  -moz-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5) inset, 0 1px 0 #fff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5) inset, 0 1px 0 #fff;

  position: absolute;
  content: '';
  left: -6px;
  right: -6px;
  top: -6px;
  bottom: -10px;
  z-index: -1;
}

.push_button:active {
  -webkit-box-shadow: 0 1px 0 rgba(255, 255, 255, 0.5) inset,
    0 -1px 0 rgba(255, 255, 255, 0.1) inset;
  top: 5px;
}
.push_button:active:before {
  top: -11px;
  bottom: -5px;
  content: '';
}

.red {
  text-shadow: -1px -1px 0 #a84155;
  background: #d25068;
  border: 1px solid #d25068;

  background-image: -webkit-linear-gradient(top, #f66c7b, #d25068);
  background-image: -moz-linear-gradient(top, #f66c7b, #d25068);
  background-image: -ms-linear-gradient(top, #f66c7b, #d25068);
  background-image: -o-linear-gradient(top, #f66c7b, #d25068);
  background-image: linear-gradient(to bottom, #f66c7b, #d25068);

  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;

  -webkit-box-shadow: 0 1px 0 rgba(255, 255, 255, 0.5) inset,
    0 -1px 0 rgba(255, 255, 255, 0.1) inset, 0 4px 0 #ad4257,
    0 4px 2px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0 1px 0 rgba(255, 255, 255, 0.5) inset,
    0 -1px 0 rgba(255, 255, 255, 0.1) inset, 0 4px 0 #ad4257,
    0 4px 2px rgba(0, 0, 0, 0.5);
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.5) inset,
    0 -1px 0 rgba(255, 255, 255, 0.1) inset, 0 4px 0 #ad4257,
    0 4px 2px rgba(0, 0, 0, 0.5);
}

.red:hover {
  background: #f66c7b;
  background-image: -webkit-linear-gradient(top, #d25068, #f66c7b);
  background-image: -moz-linear-gradient(top, #d25068, #f66c7b);
  background-image: -ms-linear-gradient(top, #d25068, #f66c7b);
  background-image: -o-linear-gradient(top, #d25068, #f66c7b);
  background-image: linear-gradient(top, #d25068, #f66c7b);
}

.blue {
  text-shadow: -1px -1px 0 #2c7982;
  background: #3eacba;
  border: 1px solid #379aa4;
  background-image: -webkit-linear-gradient(top, #48c6d4, #3eacba);
